import React, {Component} from 'react'
import { createHashHistory } from 'history'
import './map.less';



class Map extends Component{

    navigateBack=()=>{
        createHashHistory().goBack();
    }

    componentDidMount() {
        this.renderMap();
    }

    renderMap=()=>{
        let BMapGL =  window.BMapGL;
        let map = new BMapGL.Map('myMap');
        var point = new BMapGL.Point(116.404, 39.915);
        map.centerAndZoom(point, 15);
        var marker = new BMapGL.Marker(point);
        map.addOverlay(marker);
        var opts = {
            width : 300,     // 信息窗口宽度
            height: 100,     // 信息窗口高度
            title : "维修人员信息" , // 信息窗口标题
            message:"维修人员信息"
        }
        var infoWindow = new BMapGL.InfoWindow(
            `
                姓名：张三丰
                电话：1730000111
                部门：设备维修部二组
                工号：0035
            `
            , opts);
        marker.addEventListener("click", function(){
            map.openInfoWindow(infoWindow, point); //开启信息窗口
        });
    }



    render() {

        return(
            <div className='map'>
                <div className='bar'>
                    <img className='back' onClick={this.navigateBack} src={[require('../../img/back.png')]} />
                    <div className='title'>维修进度：</div>
                    <div className='status forRepair'>待维修</div>
                </div>
                <div className='container'>
                    <div id='myMap'></div>
                </div>
            </div>
        )
    }
}

export default Map;
