import PubSub from "pubsub-js";
import {get as getGlobal} from "../../utils/globalData";

export var AnimatonsType = {
    unknow: 'unknow',// 未知
    auto: 'auto'   // 自动播放
    , custom: 'custom' // 自定义播放
};

class BaseSceneAnimation {

    constructor(config) {
        this.config = config;
        this.sceneName = undefined;
        this.clipsAndActions = [];
        this.scene = undefined;
    }

    doWhenModelLoaded(event, data) {

        const sceneManager = getGlobal('sceneManager');

        this.scene = sceneManager.currentGltf.scene;

        let that = this;
        let animations = this.config;
        if (animations instanceof Array) {
            animations.forEach(function (value, index) {
                that.parser(value);//.bind(that);
            });
        }

        this.mixer = sceneManager.mixer;
        this.mixer.addEventListener('loop', function (e) {
            that.loop(e);
        });

        this.mixer.addEventListener('finished', function (e) {
            console.log('帧动画结束回调：', e);
            that.finished(e)
        });

    }

    paserWhenCustom(config, clipAndAction) {

    }

    animate(event, data) {
        // console.log('BaseScene.animate' + data.delta + ' ' + data.isLoaded);
        if (data.delta === null || data.delta === undefined) {
            console.log('BaseAniamtion.animate delta is null or undefined');
            return;
        }
        if (data.isLoaded) {
            this.updateWhenModelLoaded(data.delta);
        } else {
            this.updateWhenModelUnLoaded(data.delta);
        }
    }

    updateWhenModelLoaded(delta) {
        //todo: 获得对应的自动控制动画并播放animationAction并播放

    }

    updateWhenModelUnLoaded(delta) {

    }

    // --

    setup() {
        if (this.sceneName === undefined) {
            console.error('BaseAnimation.sceneName is undefined')
        }
        PubSub.subscribe(this.sceneName + 'loaded', this.doWhenModelLoaded.bind(this));
        PubSub.subscribe(this.sceneName + 'animate', this.animate.bind(this));
    }

    parser(config) {
        const sceneManager = getGlobal('sceneManager');
        let clipAndAction = sceneManager.getAnimationClipAndActionBy(config.name);
        if (clipAndAction === undefined){return;}
        if (clipAndAction.action === undefined){return;}
        clipAndAction.action.stop();
        if (config.type === AnimatonsType.auto) {
            clipAndAction.action.play();
        } else if (config.type === AnimatonsType.custom) {
            // let
            this.paserWhenCustom(config, clipAndAction);
            // this.clipsAndActions.push(clipAndAction);
        } else {
            console.log('unknow AnimationsType :: ' + config.type);
        }
    }

    play() {

    }

    finished(e) {

    }

    loop(e) {

    }

}

export default BaseSceneAnimation;

