import BaseAction from "./BaseAction";
import PlatformScene from "../PlatformScene";
import PubSub from "pubsub-js";

/* 站台模型 */
class PlatformAction extends BaseAction {

    constructor(config) {
        super(config);
        this.sceneName = PlatformScene.path;
        this.setup();
    }

    doWhenSingleClick(param) {
        super.doWhenSingleClick(param);
        if (this.funcName === this.pop_video.name){
            this.pop_video(this.param);
        } else {
            console.log('There is no funcation name of ' + this.funcName );
        }
    }

    pop_video(param) {

        let id = param.id;
        let src = param.src || '';
//        console.log('pop_video id is ' + id + ' src is ' + src );
        PubSub.publish(PlatformScene.path + 'pop_video', {id: id, src: src});
    }

}

PlatformAction.path = 'PlatformAction';
export default PlatformAction;