    render = () => {
        this.renderer.render(this.scene, this.camera);
    };

    onWindowResize = () => {

        this.camera.aspect = this.container.offsetWidth / this.container.offsetHeight;
        this.camera.updateProjectionMatrix();
        this.renderer.setSize(this.width, this.height);
    };

    init = () => {
        this.raycaster = new THREE.Raycaster();
        this.scene = new THREE.Scene();
        this.scene.add(this.group);

        this.camera = new THREE.PerspectiveCamera(60, this.width / this.height, 1, 2000);
        this.camera.position.x = this.defaultPositionOfCamera.x;
        this.camera.position.y = this.defaultPositionOfCamera.y;
        this.camera.position.z = this.defaultPositionOfCamera.z;
        // this.camera.lookAt( this.scene.position - new THREE.Vector3(0,0,0) );
        // this.camera.lookAt( this.scene.position);
        this.camera.lookAt(this.defaultLookAtOfCamera);

        this.renderer = new THREE.WebGLRenderer({antialias: true, alpha: true});
        this.pmremGenerator = new THREE.PMREMGenerator(this.renderer);
        this.pmremGenerator.compileEquirectangularShader();
        this.renderer.setClearColor(0xEEEEEE, 0.0);
        this.renderer.setPixelRatio(window.devicePixelRatio);
        this.renderer.setSize(this.width, this.height);
        this.renderer.toneMapping = THREE.ACESFilmicToneMapping;
        this.renderer.toneMappingExposure = 0.8;
        this.renderer.outputEncoding = THREE.sRGBEncoding;

        let ambi = new THREE.AmbientLight(0x686868); //环境光
        this.scene.add(ambi);
        let spotLight = new THREE.DirectionalLight(0xffffff);  //点光源
        spotLight.position.set(550, 100, 550);
        spotLight.intensity = 0.6;
        this.scene.add(spotLight);

        this.controls = new OrbitControls(this.camera, this.renderer.domElement);
        this.controls.addEventListener('change', this.render);
        this.controls.minDistance = 0.1;
        this.controls.maxDistance = 13;
        // this.controls.autoRotate = false;
        // this.controls.autoRotateSpeed = -10;
        this.controls.screenSpacePanning = true;
        this.controls.target.set(0, 0, 0);

        this.stats = new Stats();
        this.stats.dom.hidden = true;
        this.animate();

    };

    appendChild = (div) => {

        let newContainer = document.getElementById(div);
        if (!newContainer)
            return;

        if (newContainer !== this.container) {
            if (this.container !== null) {
                if (this.stats.dom in this.container.children) {
                    this.container.removeChild(this.stats.dom);
                }
                if (this.renderer.domElement in this.container.children) {
                    this.container.removeChild(this.renderer.domElement)
                }
                window.removeEventListener("resize", this.onWindowResize, false);
                this.ifInit = false;
            }
            this.container = newContainer;
            this.width = newContainer.offsetWidth;
            this.height = newContainer.offsetHeight;
            console.log(this.width, this.height);
            this.camera.aspect = this.width / this.height;
            this.renderer.setSize(this.width, this.height);
            this.container.appendChild(this.stats.dom);
            this.mixer = new THREE.AnimationMixer();
            this.container.appendChild(this.renderer.domElement);

            this.ifInit = true;


        }


        window.addEventListener('resize', this.onWindowResize, false);

    };

    getAnimationClipAndActionBy = (name) => {
        let animationClip = undefined;
        let animationAction = undefined;
        let clip = THREE.AnimationClip.findByName(this.currentGltf.animations, name);
        if (clip === null) return true;
        let action = this.mixer.clipAction(clip);
        if (action === null) return true;
        animationClip = clip;
        animationAction = action;
        return {clip: animationClip, action: animationAction};
    };

    setCameraPos(position, up, lookat) {

        if (position !== null) {
            this.camera.position.x = position.x;
            this.camera.position.y = position.y;
            this.camera.position.z = position.z;
        } else {
            console.log('position is null')
        }

        if (lookat !== null) {
            this.camera.lookAt(lookat);
        } else {
            console.log('lookat is null')
        }
        this.camera.lookAt(new Vector3());

        // if (up !== null) {
        //     this.camera.up = up;
        // } else {
        //     console.log('up is null')
        // }
    }

    setModelPos(position, rotation, scale) {

        if (position !== null) {
            this.defaultPositionOfModel.set(position.x, position.y, position.z);
        } else {
            console.log('position is null')
        }

        if (rotation !== null) {
            this.defaultRotationOfModel.set(rotation.x, rotation.y, rotation.z);
        } else {
            console.log('rotation is null')
        }

        if (scale !== null) {
            this.defaultScaleOfModel.set(scale.x, scale.y, scale.z);
        } else {
            console.log('scale is null')
        }

        if (this.currentGltf !== undefined) {
            this.currentGltf.scene.position.set(this.defaultPositionOfModel.x, this.defaultPositionOfModel.y, this.defaultPositionOfModel.z);
            this.currentGltf.scene.rotation.setFromVector3(new Vector3(this.defaultRotationOfModel.x, this.defaultRotationOfModel.y, this.defaultRotationOfModel.z));
            this.currentGltf.scene.scale.set(this.defaultScaleOfModel.x, this.defaultScaleOfModel.y, this.defaultScaleOfModel.z);
        }


    }

    // adjust control 、 camera near and camera far to the current model
    adjustControl() {

        this.setCameraPos(this.sceneModel.positionOfCamera,this.sceneModel.upOfCamera,this.sceneModel.lookatOfCamera);
        this.setModelPos(this.sceneModel.positionOfModel,this.sceneModel.rotationOfModel,this.sceneModel.scaleOfModel);

        let object = this.currentGltf.scene;
        const box = new THREE.Box3().setFromObject(object);
        const size = box.getSize(new Vector3()).length();
        this.controls.reset();
        // const center = box.getCenter(new Vector3());
        // object.position.x += (object.position.x - center.x);
        // object.position.y += (object.position.y - center.y);
        // object.position.z += (object.position.z - center.z);
        this.controls.maxDistance = size * 10;
        this.camera.near = size / 100;
        this.camera.far = size * 100;
        this.camera.updateProjectionMatrix();
        this.controls.saveState();
    }

    //set PMREM
    setPMREM() {
        if (SceneManager.path === DeviceScene.path) {
            this.getPmremTexture().then(({envMap}) => {
                this.scene.environment = envMap;
            });
        }
    }

    getPmremTexture() {
        let path = '/source/textures/equirectangular/venice_sunset_1k.hdr';//L1024.png
        // no envmap
        if (!path) return Promise.resolve({envMap: null});
        return new Promise((resolve, reject) => {

            new RGBELoader()
                .setDataType(THREE.UnsignedByteType)
                .load(path, (texture) => {

                    const envMap = this.pmremGenerator.fromEquirectangular(texture).texture;
                    this.pmremGenerator.dispose();

                    resolve({envMap});

                }, undefined, reject);

        });
    }

