import PubSub from "pubsub-js";
import Application‌‌ from "../Application";
import PlatformScene from "../PlatformScene";

export var TriggerType = {
    Unknow: 'Unknow',// 未知
    Auto: 'Auto'   // 自动触发
    , Single: 'single' // 单击触发
    , Double: 'Double' // 双击触发
};

/* 针对模型 */
class BaseAction {

    constructor(config) {
        this.sceneName = undefined;
        // 触发（单点、自动等）
        this.triggerName = config.triggerName;
        this.triggerType = config.triggerType;
        this.funcName = config.funcName;
        this.param = config.param;

        // 动画
        this.node = undefined;
        this.scene = undefined;

        // o.time_from_start = 0
        // o.cur_ani_index = 1
        //
        // o.ani_scene_node = nil
        // o.enable_play_animated = false

        // PubSub.subscribe(PlatformScene.path + BaseAction.path + 'action.loaded', this.doWhenModelIsLoaded.bind(this));
        // PubSub.subscribe(PlatformScene.path + BaseAction.path + 'action.picked', this.doWhenModelPicked.bind(this));

    }

    setup() {
        PubSub.subscribe(this.sceneName + 'pick', this.doWhenModelPicked.bind(this));
        PubSub.subscribe(this.sceneName + 'loaded', this.doWhenModelIsLoaded.bind(this));
        // PubSub.subscribe(this.sceneName + 'animate', this.animate.bind(this));
    }

    destory() {
        PubSub.unsubscribe(this.sceneName + 'pick');
        PubSub.unsubscribe(this.sceneName + 'loaded');
    }

    //todo:/*加载完模型后触发*/
    doWhenModelIsLoaded(event,data) {
        //data.modelName
        //todo:1、 获取节点对象 by this.triggerName ；2、如果tiggername对应的节点存在，并且是自动执行的，触发自动只是方法
        if (this.triggerName !== undefined) {
            this.node = undefined;
        }

        if (this.node !== undefined && this.triggerType === TriggerType.Auto) {
            this.doWhenAutoExecute();
        }

    }

    //todo:/*加载完模型后触发*/
    doWhenModelPicked(event, data) {
        if (data.name instanceof String) {
            // if (param.name === null || param.name.length === 0) {
            console.log('pick name error :: ' + data.name);
            return;
        }

        if (data.type instanceof String) {
            console.log('pick type error');
            return;
        }

        if (data.name !== this.triggerName) { // name is unmatch
            return;
        }

        if (data.type !== this.triggerType) { // type is unmatch
            return;
        }

        switch (this.triggerType) {
            case TriggerType.Single:
                this.doWhenSingleClick(data);
                break;
            case TriggerType.Double:
                this.doWhenDoubleClick(data);
                break;
            default:
                console.log('位置的triggerType:: ' + this.triggerType);
        }

    }

    doWhenAutoExecute(param) {

    }

    doWhenSingleClick(param) {

    }

    doWhenDoubleClick(param) {

    }

}

BaseAction.path = 'BaseAction';
export default BaseAction;