import http from '../../utils/http'
import PlatformScene from "./PlatformScene";
import DeviceScene from "./DeviceScene";
import PubSub from 'pubsub-js';
import jsonData from './config'

class Application {

    constructor() {

        this.sceneModels = [];
        this.config = undefined;
        this.platform = undefined;
        this.deviceTVM = undefined;
        this.configParser();
        PubSub.immediateExceptions = true;

    }

    configParser() {

        // http.get('/source/config.json')
        //     .then(res => {
        //         if (res.status === 200) {
        //             console.log('config.json 解析成功：：\n' + res.data.pages);
        //             this.parser(res.data.pages);
        //         } else {
        //             console.assert('config.json 解析失败');
        //         }
        //     });


        this.config = jsonData.pages;
        this.parser(this.config);
        console.log('config.json 解析成功：：\n' + this.config);

    }

    parser(pagesJsonObject) {

        let platform = new PlatformScene(pagesJsonObject.platform);
        let device_tvm = new DeviceScene(pagesJsonObject.device_tvm);
        this.platform = platform;
        this.deviceTVM = device_tvm;
        this.sceneModels.push(this.platform, this.deviceTVM);

    }

}

Application.path = 'application';
export default Application;

