import React,{Component} from 'react'
import Example from '../pages/example/device'
import Device from '../pages/device/device'
import Platform from '../pages/platform/Platform'
import SceneDemo from '../pages/sceneManagerDemo/sceneDemo'
import Map from '../pages/map/map';
import {Layout, Menu, Badge} from 'antd'
import {Link,HashRouter, Route} from 'react-router-dom'
const {  Content, Sider } = Layout;
const { SubMenu } = Menu;

class OwnLayout extends Component{


    state = {
        collapsed: false,
    };

    onCollapse = collapsed => {
        this.setState({ collapsed });
    };


    render() {
        return(
            <Layout style={{ minHeight: '100vh' }}>
                <HashRouter>
                <Sider collapsible collapsed={this.state.collapsed} onCollapse={this.onCollapse}>
                    <Menu theme="dark" defaultSelectedKeys={['platform']} mode="inline">
                        <Menu.Item key='platform'><Link to={{pathname:'/',state:{id:0}}}>站台监控</Link></Menu.Item>
                        <SubMenu
                            key="device"
                            title={<span>设备监控</span>}
                        >
                            <SubMenu
                                key='AGM'
                                title={<span>AGM</span>}
                            >
                                <Menu.Item key='AGM-1'><Link to={{pathname:'/device',state:{id:1}}}>西侧设备</Link></Menu.Item>
                                <Menu.Item key='AGM-2'><Link to={{pathname:'/device/east',state:{id:2}}}>东侧设备<Badge style={{marginLeft:'15px'}} count={2} /></Link></Menu.Item>
                            </SubMenu>
                        </SubMenu>
                    </Menu>
                </Sider>
                <Layout>
                    <Content>
                        <Route exact path='/' component={Platform} />
                        <Route exact path='/example' component={Example} />
                        <Route exact path='/device' component={Device} />
                        <Route exact path='/device/east' component={Device} />
                        <Route exact path='/sceneManager' component={SceneDemo}/>
                        <Route exact path='/map' component={Map}/>
                    </Content>
                </Layout>
                </HashRouter>
            </Layout>
        )
    }
}

export default OwnLayout;
