import React,{PureComponent} from 'react'
import Status from '../../components/status/status'
import NetworkError from '../../components/networkError/networkError'
import { createHashHistory } from 'history'
import './device.less'
import '../../img/map.png'
import http from '../../utils/http'
import {get as getGlobal} from "../../common/utils/globalData";
import {Vector2} from "three";
import SceneManager from "../../common/SceneManager";
import DeviceScene from "../../common/sceneConfig/DeviceScene";



class Device extends PureComponent{

    state={
        statusList:[],
        showError:false,
        needRepair: false
    }

    handleShow=()=>{
        createHashHistory().push('/map')
    }


    getStatus=()=>{

        let url='';
        if(this.props.history.location.state && this.props.history.location.state.id===1){
            url='/virtualData/status.json';
            this.setState({needRepair: false});
        }else {
            url= '/virtualData/status-east.json';
            this.setState({needRepair: true});
        }
        if(process.env.NODE_ENV!=='development'){
            url = 'plugin'+url
        }
        http.get(url)
            .then(res=>{
                this.setState({statusList:res.data.data || []})
            })
    };

    componentWillReceiveProps(nextProps, nextContext) {
        let url='';
        console.log(nextProps)
        let modelFileName = 'Tvm_third_bad_GLB.glb';
        if(nextProps.history.location.state && nextProps.history.location.state.id===1){
            url='/virtualData/status.json';
            modelFileName = 'Tvm_third_normal_GLB.glb';
            this.setState({needRepair: false});
        }else {
            url= '/virtualData/status-east.json';
            modelFileName = 'Tvm_third_bad_GLB.glb';
            this.setState({needRepair: true});
        }

        if(process.env.NODE_ENV!=='development'){
            url = 'plugin'+url
        }

        http.get(url)
            .then(res=>{
                this.setState({statusList:res.data.data || []});
                console.log(res.data.data)
            });

        const _this = this;
        setTimeout(()=>{
            const sceneManager = getGlobal('sceneManager');
            const application = getGlobal('Application');
            let scene = application.deviceTVM;
            SceneManager.path = DeviceScene.path;
            sceneManager.sceneModel = scene;
            sceneManager.appendChild('model');
            sceneManager.loadModelWithBg(scene.bgFilePath(), scene.bgFileName(), scene.modelFilePath(), modelFileName);

        },10)

    }

    componentWillMount() {

        this.getStatus();
    }

    componentDidMount() {
        let modelFileName = '';
        if(this.props.location.pathname==='/device/east'){
            modelFileName = 'Tvm_third_bad_GLB.glb'
        }else {
            modelFileName = 'Tvm_third_normal_GLB.glb'
        }
        console.log(this.props);
        setTimeout(()=>{
            const sceneManager = getGlobal('sceneManager');
            const application = getGlobal('Application');
            let scene = application.deviceTVM;
            SceneManager.path = DeviceScene.path;
            sceneManager.sceneModel = scene;
            sceneManager.appendChild('model');
            sceneManager.loadModelWithBg(scene.bgFilePath(), scene.bgFileName(), scene.modelFilePath(), modelFileName);
        },10);
    }

    componentWillUnmount() {
        clearTimeout();
    }

    render() {
        const { statusList,showError,needRepair } = this.state;
        return(
            <div className='device'>
                <div className='left'>
                    <div id='model'>
                        {
                            showError?<NetworkError/>:""
                        }
                    </div>
                    {
                        needRepair?
                            <div className='repair-btn' onClick={this.handleShow}>
                                <div className='icon'></div>
                                <div className='text'>一键报修</div>
                            </div>:''
                    }
                </div>
                <div className='right'>
                    <div className='position' style={{background:`url(${require('../../img/infoBG.png')})`}}>
                        <div className='title'>设备位置: 站台东侧A进口，设备3</div>
                        <div className='title'>设备ID: 030B0603   IP地址：172.3.62.103</div>
                    </div>
                    <Status list={statusList}/>
                </div>
            </div>
        )
    }
}

export default Device;
