import PubSub from 'pubsub-js'
import {Vector3} from "three";

class BaseScene {

    constructor(config) {
        this.sceneName = undefined; // set in sub class
        this.config = config;
        this.mixer = undefined;
        // camera
        let poseOfCamera = config.sceneConfig.poseOfCamera;
        // if (poseOfCamera !== undefined) {
            this.positionOfCamera = new Vector3(poseOfCamera.position.x, poseOfCamera.position.y, poseOfCamera.position.z);
            this.upOfCamera = new Vector3(poseOfCamera.up.x, poseOfCamera.up.y, poseOfCamera.up.z);
            this.lookatOfCamera = new Vector3(poseOfCamera.lookat.x, poseOfCamera.lookat.y, poseOfCamera.lookat.z);
        // }

        let poseOfModel = config.sceneConfig.poseOfModel;
        // if (poseOfModel !== undefined) {
            this.positionOfModel = new Vector3(poseOfModel.position.x, poseOfModel.position.y, poseOfModel.position.z);
            this.rotationOfModel = new Vector3(poseOfModel.rotation.x, poseOfModel.rotation.y, poseOfModel.rotation.z);
            this.scaleOfModel = new Vector3(poseOfModel.scale.x, poseOfModel.scale.y, poseOfModel.scale.z);
        // }

    }

    setup() {
        PubSub.subscribe(this.sceneName + 'animate', this.animate.bind(this));
        PubSub.subscribe(this.sceneName + 'loaded', this.doWhenModelIsLoaded.bind(this));
        PubSub.subscribe(this.sceneName + 'loadedFromCache', this.doWhenModelLoadedFromCache.bind(this));
    }

    destory() {
        PubSub.unsubscribe(this.sceneName + 'animate');
        PubSub.unsubscribe(this.sceneName + 'loaded');
    }

    doWhenModelIsLoaded(event, data) {

    }

    doWhenModelLoadedFromCache(event, data) {

    }


    animate(event, data) {
        // console.log('BaseScene.animate' + data.delta + ' ' + data.isLoaded);
        if (data.delta === null || data.delta === undefined) {
            return;
        }
        if (event.isLoaded) {
            this.animateWhenModelLoaded(data.delta);
        } else {
            this.animateWhenModelUnLoaded(data.delta);
        }
    }

    animateWhenModelUnLoaded(delta) {

    }

    animateWhenModelLoaded(delta) {

    }

    // ---
    modelFilePath() {
        if(process.env.NODE_ENV==='development'){
            return '/source/models/glb/';
        }else {
            return 'plugin/source/models/glb/'
        }
    }

    modelFileName() {
        // return 'CesiumMan.glb';
        if (this.config.sceneConfig.model_list.count <= 0) {
            console.log('model file count is 0 in config.json file');
        }
        return this.config.sceneConfig.model_list[0];
    }

    bgFilePath() {
        return '/source/textures/equirectangular/';
    }

    bgFileName() {
        return 'royal_esplanade_1k.hdr';
    }

    lightMapPath(){
        if (this.config.sceneConfig.lightMapPath.count <= 0) {
            console.log('model file count is 0 in config.json file');
        }
        return this.config.sceneConfig.lightMapPath[0];
    }

    colorMapFilePath() { // return "/source/textures/device/Frame_mat_Base_Color.jpg";
        if (this.config.sceneConfig.colorMapFilePath !== null) {
            return this.config.sceneConfig.Frame.colorMapFilePath;
        }
        return "";
    }

    metallicMapFilePath() { // return "/source/textures/device/Frame_mat_Metallic.jpg";
        if (this.config.sceneConfig.metallicMapFilePath !== null) {
            return this.config.sceneConfig.Frame.metallicMapFilePath;
        }
        return "";
    }

    roughnessMapFilePath() { // return "/source/textures/device/Frame_mat_Roughness.jpg";
        if (this.config.sceneConfig.roughnessMapFilePath !== null) {
            return this.config.sceneConfig.Frame.roughnessMapFilePath;
        }
        return "";
    }

    // colorMapFilePath(){
    //     return "";
    // }
    //
    // metallicMapFilePath(){
    //     return "";
    // }
    //
    // roughnessMapFilePath(){
    //     return "";
    // }

}

BaseScene.path = 'baseScene';

export default BaseScene;

