import BaseSceneAnimation from "./BaseSceneAnimation";
import PlatformScene from "../PlatformScene";
import * as THREE from 'three'
import PubSub from "pubsub-js";

class PlatformSceneAnimation extends BaseSceneAnimation {
    constructor(config) {
        super(config);
        // (3*60 - 23.667)/2
        this.sceneName = PlatformScene.path;
        this.action_Half_N = undefined;
        this.action_Half_S = undefined;
        this.node_Half_N = undefined;
        this.node_Half_S = undefined;
        this.time = 60;//3*
        this.timeOfAnimation = 23.67;
        this.timeMax = this.time;//3*
        this.timeWillEnter = 10;//30
        this.setup();
    }

    doWhenModelLoaded(event, data) {
        super.doWhenModelLoaded(event, data);
        this.node_Half_N = this.scene.getObjectByName('North_Tr_Dummy');
        this.node_Half_N.visible = false;
        this.node_Half_S = this.scene.getObjectByName('South_Tr_Dummy');
        this.node_Half_S.visible = false;
        this.play();
    }

    paserWhenCustom(config, clipAndAction) {
        console.log('');
        let name = config.name;
        let action = clipAndAction.action;
        if (name === 'Half_N') {
            action.setLoop(THREE.LoopOnce);
            this.action_Half_N = action;
        } else if (name === 'Half_S') {
            // action.startAt(0);
            action.setLoop(THREE.LoopOnce);
            this.action_Half_S = action;
        }
    }

    updateWhenModelLoaded(delta) {

        if (this.time >= 0) {
            this.time = this.time - delta;
        } else {
            this.play();
            this.time = this.timeMax;
        }


        // let current = '本次列车：开往安河桥北 ';
        let current = '';
        if (this.time <= this.timeMax && this.time > this.timeMax - this.timeOfAnimation ) {// 已进站
            current += '已进站';
        } else if (this.time <= this.timeWillEnter ) {//即将进站
            current += '即将进站';
        } else {//
            current += '等待' + Math.ceil(this.time/ 60) + '分钟';
        }

        // let next = '下一班列车还需等待：' + (this.timeMax - this.time)/60 + '分钟';
        let next = Math.ceil((this.timeMax + this.time ) / 60);

        PubSub.publish(PlatformScene.path + 'report', {current: current, next: next});

    }

    // ---

    play() {
        // super.play();

        // this.action_Half_N.setDuration(3);
        // this.action_Half_S.setDuration(3);

        this.node_Half_S.visible = true;
        this.playActionOnce(this.action_Half_S);

    }

    finished(e) {
        if (e.action === this.action_Half_S) {
            this.node_Half_S.visible = false;
            this.node_Half_N.visible = true;
            this.playActionOnce(this.action_Half_N);
        } else if (e.action === this.action_Half_N) {
            this.node_Half_N.visible = false;
        }
    }

    playActionOnce(action, delay = 0) {
        if (action !== undefined) {
            action.reset();
            action.setLoop(THREE.LoopOnce, undefined);
            // action.startAt(delay);
            action.play();
        } else {
            console.log('action is undefined');
        }
    }

    playActionRepeat(action, delay = 0) {
        if (action !== undefined) {
            // action.reset();
            action.setLoop(THREE.LoopRepeat, Infinity);
            action.startAt(delay);
            action.play();
        } else {
            console.log('action is undefined');
        }
    }

}

export default PlatformSceneAnimation;