import BaseScene from './BaseScene'
import PlatformAction from "./action/PlatformAction";
import PlatformSceneAnimation from "./animations/PlatformSceneAnimation";


class PlatformScene extends BaseScene {

    constructor(config) {

        super(config);

        this.sceneName = PlatformScene.path;

        // parse actions
        this.platformActions = [];
        let that = this;
        let actionList = config.sceneConfig.triggers.actions;
        if (actionList instanceof Array){
            actionList.forEach(function (value,index) {
                let action = new PlatformAction(value);
                that.platformActions.push(action);
            });
        }

        this.sceneAniamtion = new PlatformSceneAnimation(this.config.sceneConfig.animations);

        this.setup();

    }

    doWhenModelIsLoaded(event, data) {
        // data.modelName
        super.doWhenModelIsLoaded(event, data);
    }

    animate(event,data) {
        // console.log('PlatformScene.animate' + data.delta + '  ' + data.isLoaded);
    }

    animateWhenModelLoaded(delta) {
    }

    animateWhenModelUnLoaded(delta) {
    }

}

PlatformScene.path = 'platform.';

export default PlatformScene;