import React from 'react';
import ReactDOM from 'react-dom';
import * as THREE from "three";

import Application from './common/sceneConfig/Application'
import SceneManager from './common/SceneManager'

import {set,get} from './common/utils/globalData'

import './index.css';
import App from './App';
import * as serviceWorker from './serviceWorker';

set("EventDispatcher", new THREE.EventDispatcher());
set("Application", new Application());

set("sceneManager", new SceneManager());
get("sceneManager").init();



ReactDOM.render(<App />, document.getElementById('root'));

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
