import React, {PureComponent} from 'react'
import {get as getGlobal} from "../../common/utils/globalData";
import './status.less'


class Status extends PureComponent {

    state = {
        list: [],
        selected: -1
    };

    errorJudjement = (list) => {
        for (let i = 0; i < list.length; i++) {
            if (list[i].status === "error")
                return false
        }
        return true;
    };

    handleClick = (index) => {


// Distribution_Unit 发行 2.376620357977175 -0.09920244780879667 -0.004236099526651743 == -0.0349794605003186 -0.4282042984837279 -0.07555937145275725
// UPS 1.1525563776335424 -1.5962918241672834 -0.2946352996746073 == -0.37367151436281076 -1.7364980079953276 -0.21646172177968243
// Render_unit 读卡器 0.8931672573320442 1.396145436359538 -0.08890383748009252 == 0.15497381567892415 1.2365884815192894 -0.08348527499470576
// Banknote_unit 纸币单元 -1.9524789679123824 -0.17753405117771476 2.9963349371220436 == 0.5108138866892379 -0.3429119959990824 0.5340557086551816
// coin_unit 硬币单元 -2.1019016186189075 -0.6410775169220737 2.922886865182427 == 0.0630504553140941 -0.7882444661038277 -0.03731093237479279


        const sceneManager = getGlobal('sceneManager');
        let newP = {x: 0.7, y: 0.35, z: 1.6};
        let newT = {x: 0, y: 0, z: 0};
        let name = '';
        switch (index) {
            case 0:
                name = 'frame_unit';
                newP = {x: 0.12, y: 0.58627, z: 5.5};
                newT = {x: 0, y: 0, z: 0};
                break;
            case 1:
                name = 'Banknote_unit';
                newP = {x: -1.9524789679123824, y: -0.17753405117771476, z: 2.9963349371220436};
                newT = {x: 0.5108138866892379, y: -0.3429119959990824, z: 0.5340557086551816};
                break;
            case 2:
                name = 'coin_unit';
                newP = {x: -2.1019016186189075, y: -0.6410775169220737, z: 2.922886865182427};
                newT = {x: 0.0630504553140941, y: -0.7882444661038277, z: -0.03731093237479279};
                break;
            case 3:
                name = 'Distribution_Unit';
                newP = {x: 2.376620357977175, y: -0.09920244780879667, z: -0.004236099526651743};
                newT = {x: -0.0349794605003186, y: -0.4282042984837279, z: -0.07555937145275725};
                break;
            case 4:
                name = 'UPS';
                newP = {x: 1.1525563776335424, y: -1.5962918241672834, z: -0.2946352996746073};
                newT = {x: -0.37367151436281076, y: -1.7364980079953276, z: -0.21646172177968243};
                break;
            case 5:
                name = 'Render_unit';
                newP = {x: 0.8931672573320442, y: 1.396145436359538, z: -0.08890383748009252};
                newT = {x: 0.15497381567892415, y: 1.2365884815192894, z: -0.08348527499470576};
                break;
            default:
                break;
        }
        sceneManager.hideUnitWithOut(name);
        sceneManager.animateCamera(sceneManager.camera.position, sceneManager.controls.position0, newP, {
            x: 0,
            y: 0,
            z: 0
        }, newT);
        this.setState({selected: index})
    };

    componentWillReceiveProps(nextProps, nextContext) {
        this.setState({list: nextProps.list || []})
    }

    render() {
        const {list, selected} = this.state;
        return (
            <div className='status'>
                {
                    list.map((item, index) => {
                        return (
                            <div
                                className={selected === index ? "item-selected" : "item"}
                                style={{background: selected === index ? `url(${require('../../img/bg-act-tall.png')})` : `url(${require('../../img/bg-tall.png')})`}}
                                key={index}
                                onClick={() => this.handleClick(index)}>
                                <div className={this.errorJudjement(item.list) ? "left" : "left-err"}>{item.title}</div>
                                <div className='line'></div>
                                <div className='right'>
                                    {
                                        item.list.map((tip, tipIndex) => {
                                            return (
                                                <div className={tip.status === "error" ? "tip-err" : "tip"}
                                                     key={tipIndex}>
                                                    <div className='key'>{tip.key}</div>
                                                    <div className='value'>{tip.value}</div>
                                                </div>
                                            )
                                        })
                                    }
                                </div>
                            </div>
                        )
                    })
                }
            </div>
        )
    }
}

export default Status;


