import React, {Component} from 'react'
import Modal from '../../components/modal/modal'
import './platform.less'
import {get as getGlobal} from "../../common/utils/globalData";
import {Vector2} from "three";
import SceneManager from "../../common/SceneManager";
import PlatformScene from "../../common/sceneConfig/PlatformScene";
import PubSub from "pubsub-js";


class Platform extends Component {

    // -- 3D

    handlePick = (e) => {
        const sceneManager = getGlobal('sceneManager');
        const node = sceneManager.pick(new Vector2(224, 44), e.clientX, e.clientY);
    };

    // -- video

    handleOpenVideo = () => {
        this.setState({
            show: true
        })
    };

    handleCancelModal = () => {
        this.setState({
            show: false,

        })
    };


    state = {
        show: false,
        video: '/source/video/bg_video.mp4',
        currentReport:'本次列车：开往会议中心',
        nextReport:'2',
        sceneManager: null
    };

    componentWillMount() {
        PubSub.subscribe(PlatformScene.path + 'pop_video', this.showVideo.bind(this));
        PubSub.subscribe(PlatformScene.path + 'report', this.report.bind(this));
        // PubSub.publish(PlatformScene.path + 'report', {current: current, next: next});

    };

    componentDidMount() {

        const _this = this;
        setTimeout(()=>{
            const sceneManager = getGlobal('sceneManager');
            const application = getGlobal('Application');
            let scene = application.platform;
            SceneManager.path = PlatformScene.path;
            sceneManager.sceneModel = scene;
            sceneManager.appendChild('platform-demo');
            sceneManager.loadModelWithBg(scene.bgFilePath(), scene.bgFileName(), scene.modelFilePath(), scene.modelFileName());
            sceneManager.container.addEventListener('click', _this.handlePick, false);
            // sceneManager.setCameraPos(scene.positionOfCamera,scene.upOfCamera,scene.lookatOfCamera);
            // sceneManager.setModelPos(scene.positionOfModel,scene.rotationOfModel,scene.scaleOfModel);

        },10)
    };


    componentWillUnmount() {
        const sceneManager = getGlobal('sceneManager');
        const _this = this;
        if (sceneManager.container !== null) {
            sceneManager.container.removeEventListener('click', _this.handlePick, false);
        }
    };

    showVideo(event ,data){
        this.setVideo(data.src);
        this.handleOpenVideo();
    }

    setVideo = (newViedeo) => {
        this.setState({
            video: newViedeo
        })
    };

    render() {
        const {show, video,currentReport,nextReport} = this.state;
        return (
            <div className='platform'>
                <Modal
                    show={show}
                    onCancel={this.handleCancelModal}
                    title="XXXj 监控实时效果"
                >
                    <video className='video' controls={false} autoPlay loop src={video}>您的浏览器不支持video标签，建议更换现代浏览器
                    </video>
                </Modal>
                <div id='model'>
                    <div id = 'platform-demo' className='platform-demo' >

                    </div>
                    <div className='info-card'>
                        <div className='info-container'>
                            <span className='title'>本次列车：</span>
                            <span className='info'>开往会议中心</span>
                            <span className='status'>{currentReport}</span>
                        </div>
                        <div className='info-container'>
                            <span className='title'>下一班列车还需等待：</span>
                            <span className='info'>{nextReport}分钟</span>
                        </div>
                    </div>
                </div>
            </div>
        )
    };

    //{this.props.location.state.id}
    report(event,data){
        this.setState({
            currentReport: data.current,
            nextReport: data.next
        })
    }
}

export default Platform;
