import React, { PureComponent } from 'react'
import './sceneDemo.less'
import {get as getGlobal} from '../../common/utils/globalData'
import {Vector2} from "three";


class SceneDemo extends PureComponent{

    state={
        sceneManager: null
    }

    handlePick=(e)=>{
        const sceneManager = getGlobal('sceneManager');
        const node = sceneManager.pick(new Vector2(200, 0), e.clientX, e.clientY);
        if(node!==null){
             sceneManager.animateCamera(sceneManager.camera.position, sceneManager.controls.position0,node.object.position,node.object.position)
        }
        console.log(node);
    }

    componentDidMount() {
        const _this = this;
        const sceneManager = getGlobal('sceneManager');
        sceneManager.appendChild('sceneDemo');
        sceneManager.loadModelWithBg('/source/textures/equirectangular/','royal_esplanade_1k.hdr','/source/models/glb/','CesiumMan.glb');
        sceneManager.container.addEventListener('click',_this.handlePick,false);
    }

    componentWillUnmount() {
        const sceneManager = getGlobal('sceneManager');
        const _this = this;
        if(sceneManager.container!==null){
            sceneManager.container.removeEventListener('click',_this.handlePick,false);
        }
    }

    render() {

        return(
            <div id='sceneDemo'></div>
        )
    }

}



export default SceneDemo
